* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    background-image: url("/src/styles/images/pexels3.jpg");
    background-size: 100%;
    font-family: "Open Sans", serif;
    font-size: 17px;
    padding: 80px;
    color: white;
  }
  
  .box {
    margin-top: 80px;
    padding: 35px;
    background-color: black;
    border: black;
  }
  
  h1 {
    font-size: 35px;
    margin: 75px;
    text-align: center;
  }
  
  h2 {
    font-size: 25px;
    font-weight: 300;
    margin: 25px;
  }
  
  p {
    margin-bottom: 20px;
  }
  
  a {
    background-color: black;
  }
  
  nav {
    width: 100%;
    background-color: black;
    overflow: hidden;
  }
  
  nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  nav ul li {
    display: inline-block;
  }
  
  .nav-left {
    float: left;
  }
  
  .nav-right {
    float: right;
  }
  
  .nav-left a:hover {
    background-color: white;
    color: #333;
  }
  
  .nav-left a.active {
    background-color:  #333;
    color: white;
  }
  
  .nav-left a {
    float: left;
    text-align: center;
    padding: 12px;
    color: white;
    text-decoration: none;
    font-size: 17px;
  }
  
  .nav-right a:hover {
    background-color: #333;
    color: white;
  }
  
  .nav-right a {
    float: left;
    text-align: center;
    padding-right: 5px;
    padding-left: 5px;
    background-color: white;
    text-decoration: none;
    font-size: 17px;
  }
  
  @media screen and (max-width: 500px) {
    .nav-left a {
      float: none;
      display: block;
    }
  }
  
  @media screen and (max-width: 500px) {
    .nav-right a {
      float: none;
      display: block;
    }
  }

  * {
    box-sizing: border-box;
  }
  
  /* Position the image container (needed to position the left and right arrows) */
  .container {
    position: relative;
  }
  
  /* Hide the images by default */
  .mySlides {
    display: none;
  }
  
  /* Add a pointer when hovering over the thumbnail images */
  .cursor {
    cursor: pointer;
  }
  
  /* Next & previous buttons */
  .prev,
  .next {
    cursor: pointer;
    position: absolute;
    top: 40%;
    width: auto;
    padding: 16px;
    margin-top: -50px;
    color: white;
    font-weight: bold;
    font-size: 20px;
    border-radius: 0 3px 3px 0;
    user-select: none;
    -webkit-user-select: none;
  }
  
  /* Position the "next button" to the right */
  .next {
    left: 960;
    border-radius: 3px 0 0 3px;
  }
  
  /* On hover, add a black background color with a little bit see-through */
  .prev:hover,
  .next:hover {
    background-color: rgba(0, 0, 0, 0.8);
  }
  
  /* Number text (1/3 etc) */
  .numbertext {
    color: #f2f2f2;
    font-size: 12px;
    padding: 8px 12px;
    position: absolute;
    top: 0;
  }
  
  /* Container for image text */
  .caption-container {
    text-align: center;
    background-color: #222;
    padding: 2px 16px;
    color: white;
  }
  
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Six columns side by side */
  .column {
    float: left;
    width: 47.6px;
    height: 30px;
  }
  
  /* Add a transparency effect for thumnbail images */
  .demo {
    opacity: 0.6;
  }
  
  .active,
  .demo:hover {
    opacity: 1;
  }

  div.column.img { width: 50%; }